*{
	box-sizing: border-box;
}

.visually-hidden:not(:focus):not(:active),
input[type='checkbox'].visually-hidden,
input[type='radio'].visually-hidden{
	position: absolute;

	width: 1px;
	height: 1px;
	margin: -1px;
	border: 0;
	padding: 0;

	white-space: nowrap;

	clip-path: inset(100%);
	clip: rect(0 0 0 0);
	overflow: hidden;
}

.App {
	background-color: #151619;
	font-family: 'Inter', sans-serif;
	font-style: normal;
	color: #F2F2F2;
}

a{
	color: inherit;
	text-decoration: none;
	cursor: pointer;
}

button{
	cursor: pointer;
}

